<template>
  <v-responsive class="" id="AboutMe">
    <v-container>
      <Title :title="'About_Me'" bg=""></Title>
      <v-row class="mt-10 flex-container d-flex text-center" justify="center">
        <v-col cols="12" md="6">
          <v-card color="transparent" flat >
            <v-img v-if="$vuetify.theme.dark"  :src="require('@/assets/img/ellipse_rezise.png')" style="position: absolute; margin-top: -5vmax;"></v-img>
            <v-flex>
              <center><v-img :src="require('@/assets/img/photo-about.png')" :width="isMobile ? '100%':'70%'"></v-img></center>
            </v-flex>
          </v-card>
        </v-col>
        <v-col cols="12" md="6" :class="isMobile ? 'kanan':'kiri'">
          <v-row :class="isMobile ? '' :'about'">
            <v-card elevation="5" class="mt-auto mb-0 mx-auto my-8 bgExperience rounded-xxl rounded-bl-0 rounded-tr-0">
              <v-card-title>
                <h3>Imam Bukhari</h3>
            </v-card-title>
            <v-spacer></v-spacer>
            <v-card-text>
              <p v-scrollanimation class="text-justify">{{ $t('pengalaman')}}</p>
            </v-card-text>
            </v-card>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-responsive>
</template>

<script>
import Title from '../Title.vue'
export default {
  name: 'AboutMe',
  components: { Title },
  data: () => ({
    isMobile: false
  }),
  mounted () {
    this.isMobile = window.innerWidth < 868
    window.addEventListener('resize', this.checkIsMobile)
  },
  methods: {
    checkIsMobile () {
      this.isMobile = window.innerWidth < 868
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.checkIsMobile)
  }
}
</script>
