<template>
  <div>
    <Profile :is-mobile="isMobile"/>
    <AboutMe/>
    <Skill/>
    <!-- <Pengalaman/> -->
    <!-- <Portfolio/> -->
    <Join/>
    <Footer/>
    <v-bottom-navigation
      :value="value"
      color="blue"
      fixed
      grow
      v-if="isMobile || isIpad"
    >
      <v-btn v-for="(menu, i) in menus" :href="menu.url" :key="i" v-show="i < 2">
        <span>{{ menu.title }}</span>

        <v-icon>{{ `${'mdi-'+ menu.icon}` }}</v-icon>
      </v-btn>
      <v-bottom-sheet v-model="sheet">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          dark
          v-bind="attrs"
          v-on="on"
        >
        More
        <v-icon>mdi-dots-horizontal-circle</v-icon>
        </v-btn>
      </template>
      <v-list>
        <div class="text-center">
          <v-icon large @click="sheet = false">mdi-download-circle-outline</v-icon>
        </div>
        <v-list-item
          v-for="tile in menus"
          :key="tile.title"
          :href="tile.url"
          @click="sheet = false"
        >
          <v-list-item-avatar>
            <v-avatar
              size="32px"
              tile
            >
            <v-icon>{{ `${'mdi-'+ tile.icon}` }}</v-icon>
              <!-- <img
                :src="`https://cdn.vuetifyjs.com/images/bottom-sheets/${tile.img}`"
                :alt="tile.title"
              > -->
            </v-avatar>
          </v-list-item-avatar>
          <v-list-item-title>{{ tile.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>
    </v-bottom-navigation>
  </div>
</template>

<script>
import { mapState } from 'vuex'
// import Pendidikan from '@/components/homeView/Pendidikan.vue'
import Profile from '@/components/homeView/Profile.vue'
import Skill from '@/components/homeView/Skill.vue'
// import Pengalaman from '@/components/homeView/Pengalaman.vue'
// import Portfolio from '@/components/homeView/Portfolio.vue'
import Join from '@/components/Join.vue'
import Footer from '@/components/Footer.vue'
import AboutMe from '@/components/homeView/AboutMe.vue'

export default {
  name: 'HomeView',
  components: {
    Profile, Skill, Join, Footer, AboutMe
  },
  data: () => ({
    value: 1,
    isMobile: false,
    isIpad: false,
    sheet: false
  }),
  computed: {
    ...mapState('home', {
      menus: (state) => state.menus
    })
  },
  mounted () {
    this.isMobile = window.innerWidth < 600
    this.isIpad = window.innerWidth < 868
    window.addEventListener('resize', this.checkIsMobile)
    window.addEventListener('resize', this.checkIsIpad)
  },
  methods: {
    checkIsMobile () {
      this.isMobile = window.innerWidth < 600
    },
    checkIsIpad () {
      this.isIpad = window.innerWidth < 868
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.checkIsMobile)
    window.removeEventListener('resize', this.checkIsIpad)
  }
}
</script>
