<template>
  <v-menu class="mr-2 switch-lang">
    <template v-slot:activator="{ on, attrs }">
      <img
        class="lang-menu-activator"
        :src="getIcon()"
        v-bind="attrs"
        v-on="on"
      />
    </template>
    <v-list>
      <v-list-item
        v-for="(lang, index) in langs"
        :key="index"
        class="lang-menu-item"
        @click="setLang(lang.value)"
      >
        <div class="d-flex align-center">
          <img style="width: 30px"
                :src="lang.iconSrc"
                class="mr-2"/>
          <span>{{ lang.text }}</span>
        </div>
      </v-list-item>
    </v-list>
  </v-menu>
</template>
<script>
export default {
  data () {
    return {
      selectedLang: 'en',
      langs: [
        {
          value: 'en',
          text: 'English',
          iconSrc: require('@/assets/img/language/en.png')
        },
        {
          value: 'id',
          text: 'Indonesia',
          iconSrc: require('@/assets/img/language/id.png')
        }
      ]
    }
  },
  computed: {
    getIcon () {
      return (item) => {
        const image = item || this.selectedLang
        let iconSrc = null
        Object.keys(this.langs).forEach((index) => {
          if (this.langs[index].value === image) {
            iconSrc = this.langs[index].iconSrc
          }
        })
        return iconSrc
      }
    }
  },
  methods: {
    setLang (lang) {
      this.$i18n.locale = lang
      this.selectedLang = lang
      localStorage.setItem('lang', lang)
    }
  },
  mounted () {
    const lang = localStorage.getItem('lang')
    if (lang) {
      this.selectedLang = lang
      this.$i18n.locale = lang
    } else {
      localStorage.setItem('lang', 'en')
      this.$i18n.locale = 'en'
    }
  }
}
</script>
<style>
.switch-lang{
  z-index: 1001 !important;
  display: block !important;
}
.d-flex{
  display: flex;
}

.align-center{
  align-items: center;
}

.mr-2{
  margin-right: 8px;
}

.mt-4{
  margin-top: 16px;
}

.lang-menu-activator{
  width: 30px;
  cursor: pointer;
  z-index: 10;
}
.lang-menu-item{
  cursor: pointer hover;
    /* background-color: #fafafa */
}

</style>
