<template>
  <v-app>
    <v-row class="pr-0 pl-auto">
    </v-row>
    <nav class="toolbar" align="center">
      <v-app-bar app color="warnaNavbar"
      elevate-on-scroll>
        <v-container>
          <v-row align="center">
            <div class="d-flex align-center">
              <v-avatar
                color="blue"
                size="36"
              ><span class="white--text text-h6">IB</span></v-avatar>
              <v-toolbar-title class="ml-2">{{ profile.name }}</v-toolbar-title>
            </div>
            <v-spacer />
            <div class="hidden-sm-and-down">
              <span v-for="(menu, i) in menus" :key="i" style="z-index: 1000; margin-right: 30px" v-show="i < 4">
                <a :href="menu.url" @click="pilih(menu.key)" v-bind:class="{ active: (menu.key === activeId) }" style="text-decoration: none;color: inherit;">{{ $t(`menu.${menu.key}`) | upper }}</a>
              </span>
            </div>
            <v-spacer />
            <a href="#Contact" @click="pilih('Contact')">
              <v-btn text outlined class="text-none text-subtitle-1 shrink mt-1 hidden-sm-and-down mr-4">
                <span class="mr-2">{{ $t('menu.Contact') | upper }}</span>
              </v-btn>
            </a>
              <v-icon v-if="$vuetify.theme.dark" @click="lightbulb()" class="shrink mr-2">mdi-star-crescent</v-icon>
              <v-icon v-else @click="lightbulb()" class="shrink mr-2">mdi-weather-sunny</v-icon>
              <SwitchLang class="shrink"/>
              <!-- <v-app-bar-nav-icon class="hidden-lg-and-up"></v-app-bar-nav-icon> -->
          </v-row>
        </v-container>
      </v-app-bar>
    </nav>
    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import SwitchLang from './components/SwitchLang.vue'
export default {
  name: 'App',
  components: {
    SwitchLang
  },
  data: () => ({
    activeId: 'Profile'
  }),
  computed: {
    ...mapState('home', {
      menus: (state) => state.menus,
      profile: (state) => state.profile
    })
  },
  mounted () {
    const theme = localStorage.getItem('dark_theme')
    if (theme) {
      if (theme === 'true') {
        this.$vuetify.theme.dark = true
      } else {
        this.$vuetify.theme.dark = false
      }
    } else {
      this.$vuetify.theme.dark = false
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    }
  },
  filters: {
    upper (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  },
  methods: {
    pilih (id) {
      this.activeId = id
    },
    lightbulb () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    },
    getContact () {
      const editButtonRef = this.$refs.contact
      editButtonRef.focus()
    }
  }
}
</script>
<style lang="scss">
  @import './app.scss';
</style>
